

.user-info-wrapper {
    background-color: white;
    padding: 100px 100px 70px;
    border-radius: 15px;
    display: flex;
    gap: 40px;
    justify-content: space-around;

    .user-photo {
        border-radius: 50%;
    }
    .user-text-wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;

        .name-wrapper {
            display: flex;
            gap: 10px;
            align-items: center;

            .user-name {
                font-size: 30px;
                margin: 0px;
            }
            .username-text {
                font-size: 20px;
            }
        }
        .user-company-wrapper {
            display: flex;
            flex-direction: column;
            gap: 3px;
            margin-bottom: 10px;
            font-size: 14px;

            .user-company-item {
                font-size: 17px;
            }
            .user-web-item {
                text-decoration: none;
                color: #4F4F4F;
            }
            .user-web-item:hover {
                color: rgb(163, 149, 125);
            }
        }
        .user-contacts-list {
            list-style-type: none;
            font-weight: 700;
            padding-left: 0;
            margin: 50px 0 25px;

            .user-list-link {
                text-decoration: none;
                color: #4F4F4F;
                font-weight: 400;
                font-size: 15px;
            }
            .user-list-link:hover {
                color: rgb(163, 149, 125);
            }
        }
        .user-address-wrapper {
            margin: 0;
            font-weight: 400;
            font-size: 15px;

            .adress-link {
                text-decoration: none;
                color: #959595;
            }
            .adress-link:hover {
                color: rgb(199, 180, 148);
            }
        }
    }
}